.miniresul{
    width: 100px;
    height: 80px;
}

.resultados {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    vertical-align: center;
    text-align: center;
    margin-bottom: 50px;
    background-color: rgba(255, 255, 255, 0.7);
    margin-right: 15px;
    margin-left: 15px;
}

.jornada{
    background-color: #212121;
    color: #00BCD4;
    font-weight: bold;
    margin: auto 0;
    padding: 50px 100px;
}

.partido{
    border-right: 2px solid #212121;
    padding-left: 10px;
    padding-right: 10px;
}

.partido2{
    padding-left: 10px;
    padding-right: 10px;
}

h4{
    margin-left: 15px;
    padding: 10px 10px 5px 10px;
    border-left: 7px solid red;
    border-bottom: 7px solid red;
    border-top: 7px solid transparent;
    border-right: 7px solid transparent;
    width: fit-content;
    margin-bottom: 20px !important;
    font-weight: bold !important;
}

.espacioclas{
    margin-top: 30px;
}

.avatar{
    height: 170px;
    width: 200px;
}

.clasif{
    display: grid;
    grid-template-columns: 1fr 3fr 1fr;
    text-align: center;
    margin: 15px;
    border: 3px solid #212121;
    border-radius: 7px;
    padding: 0px;
    font-weight: bold;
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);

}

.puesto{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 42px;
    padding-left: 20px;
    padding-right: 20px;
    background-color: #212121;
    color: white;
    height: 100%;
}

.pareja{
    padding: 0 20px;
    background-color: rgba(255, 255, 255, 0.7);
}

.datos{
    display: grid;
    grid-template-row: 50% 50%;
    align-items: stretch;
}

.victorias{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(150, 255, 150, 0.7);
    padding: 0 25px;
}

.puntos{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(255, 255, 0, 0.6);
    padding: 0 25px;
}

.iconoVictorias{
    margin-left: 10px;
    padding: 10px 19px;
    font-weight: bold;
    font-size: 24px;
    border-radius: 35px;
    background-color: #00BCD4;
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
    color: white;
}

.iconoPuntos{
    margin-left: 10px;
    padding: 10px 20px;
    font-weight: bold;
    font-size: 24px;
    border-radius: 35px;
    background-color: orange;
    box-shadow: 5px 5px 5px 0px rgba(0,0,0,0.75);
}

.fuente{
    font-size: 48px;
}

.footer{
    color: white;
    bottom: 0;
    background-color: #212121;
    text-align: center;
    padding: 30px 0;
    margin-top: 50px;
}

a{
    font-weight: bold;
}

a:hover{
    font-size: 25px;
}

body{
    background-image: url("./img/fondo.jpg");
    background-attachment: fixed;
}

@media only screen and (max-width: 1100px) {
    .resultados{
        overflow: auto;
        white-space: nowrap;
    }

    .clasif{
        overflow: auto;
        white-space: nowrap;
    }
}